import React from "react";
import { Provider } from "react-redux";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import store from "../store/store";

export default () =>
  <Provider store={store}>
    <Router>
      <Switch>
        <Route exact path="/">
          <h1>Hello</h1>
        </Route>
      </Switch>
    </Router>
  </Provider>;

  
